@import url("https://fonts.googleapis.com/css2?family=Asap:wght@400;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* settings for Nav-bar */

.nav-bar {
  position: fixed;
  height: 60px;
  width: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.7);
}

.nav-bar-list {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.nav-bar-list li,
.side-bar-container ul li {
  list-style-type: none;
  padding: 5px 10px;
  margin: 0px 20px;
}

.nav-bar-list li a,
.side-bar-container ul li a {
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(238, 237, 237);
  font-weight: 600;
  text-decoration: none;
  text-transform: uppercase;
}

.nav-bar-list li a:hover {
  color: white;
  cursor: pointer;
}

.expand-icon {
  color: rgb(238, 237, 237);
  transition: all 0.4s ease;
}

.expand-icon:hover {
  cursor: pointer;
}

.expand-icon-cell {
  display: none;
}

/* Home Page */
.home-container img#home-bg {
  min-height: 100%;
  min-width: 1000px;
  width: 100%;
  height: auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
}

.home-brief-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100vh;
}

.home-brief-card#about {
  background: #232526;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right,
      #414345,
      #232526);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right,
      #414345,
      #232526);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.home-brief-card#projects {
  background: #457fca;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right,
      #5691c8,
      #457fca);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right,
      #5691c8,
      #457fca);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.home-brief-card#contact {
  background: #b79891;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right,
      #94716b,
      #b79891);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right,
      #94716b,
      #b79891);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.home-brief-card h1 {
  font-size: 5rem;
  color: white;
}

.home-brief-card p {
  font-size: 1.5rem;
  color: white;
}

.next-page-icon {
  position: absolute;
  bottom: 30px;
  transition-property: bottom;
  transition-duration: 0.2s;
  transition-timing-function: ease-out;
  transition-delay: 0;
}

.next-page-icon:hover {
  bottom: 25px;
}

.next-page-icon i {
  color: white;
}

/* cursor */
.type-effect {
  border-right: 3px solid #f24405;
}

/* about me */
.about-me-page {
  display: grid;
  grid-template-columns: 250px auto;
  gap: 0px 40px;
}

.about-me-page .about-me-content {
  grid-column-start: 2;
  margin-right: 50px;
  font-family: Arial, Helvetica, sans-serif;
  color: #5a6473;
}

.summary {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.summary .name {
  font-family: "Bebas Neue", cursive;
  font-size: 10rem;
  margin: 0;
}

.summary .name>.firstname {
  color: #343a40;
}

.summary .name>.lastname {
  color: #e77557;
}

.summary .email {
  font-family: "Bebas Neue", cursive;
  font-size: 2rem;
  margin-top: -30px;
}

.summary .bio {
  font-size: 1.3rem;
  padding: 20px 0 120px 0;
}

.about-me-content>p.topic {
  font-family: "Bebas Neue", cursive;
  font-size: 5rem;
  color: #343a40;
  padding-top: 3rem;
}

/* about section flash-card */
.about-card-container {
  display: grid;
  grid-template-columns: 2fr 1fr;
  margin-bottom: 5rem;
}

.about-card-container>.period {
  grid-column-start: 2;
  grid-row-start: 1;
  grid-row-end: 4;
  font-size: 1.2rem;
  color: #e77557;
}

.about-card-container>.topic {
  font-family: "Bebas Neue", cursive;
  font-size: 2.5rem;
  margin-bottom: 0;
  color: #343a40;
}

.about-card-container>.option {
  font-family: "Bebas Neue", cursive;
  font-size: 1.8rem;
}

.about-card-container>.content {
  font-size: 1.2rem;
}

.about-card-container>.content>.detail-list {
  margin-left: 15px;
}

.about-card-container>.content>.detail-list li {
  font-size: 1.1rem;
  padding: 0.05rem;
}

.about-card-container>.content>.additional {
  padding-top: 1.5rem;
}

/* progress bar section */
.skills-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 120px;
}

.progress-bar-wrapper>header {
  font-family: "Bebas Neue", cursive;
  font-size: 2rem;
  margin: 30px 0;
}

.progress-bar {
  padding-bottom: 15px;
}

.progress-bar>.outer {
  height: 15px;
  width: 90%;
  border-radius: 5px;
  z-index: -1;
  background: #f2f2f2;
}

.progress-bar>.outer>.inner {
  height: 100%;
  width: 0;
  transition: all 2s ease;
  background: #e77557;
  border-radius: 5px;
}

/* SideBar */
.side-bar-container {
  position: fixed;
  z-index: 1;
  height: 100vh;
  width: 250px;
  background-color: #385d84;
}

.side-bar-container .back-to-home {
  padding: 15px 0;
  text-align: center;
  background-color: #e77557;
}

.side-bar-container .back-to-home a {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
}

.side-bar-container .side-bar-active,
.header-bar-container .side-bar-active {
  color: #e77557;
}

.side-bar-container .side-bar {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
}

.side-bar-container ul li {
  padding: 0.5rem;
  margin: 0.5rem;
}

.side-bar-container ul li a {
  font-size: 1.2rem;
  color: #f2f2f2;
  letter-spacing: 0.05rem;
  transition: color 0.3s ease;
}

.side-bar-image {
  border-radius: 50%;
  border: 3px solid #f2f2f2;
  margin-bottom: 1rem;
}

.link-out i {
  color: #f2f2f2;
  margin-top: 50px;
}

.link-out i:hover {
  color: white;
  cursor: pointer;
}

.side-bar-container ul li a:hover,
.header-bar-container ul li a:hover {
  color: #f0937e;
  cursor: pointer;
}

/* small-bar */
.header-bar-container {
  position: fixed;
}

.header-bar-container .toggler {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99999;
  cursor: pointer;
  width: 50px;
  height: 50px;
}

.header-bar-icon {
  position: fixed;
  width: 50px;
  height: 50px;
  z-index: 999;
  color: #004f6e;
  padding: 10px;
  text-align: center;
  margin: 10px;
  background-color: rgba(0, 0, 0, 0);
}

.header-bar-icon.home {
  top: 50px;
}

.header-bar-icon.home i {
  color: #004f6e;
}

.header-bar-icon i {
  margin: 0 auto;
  transition: transform 0.5s ease;
}

.header-bar-icon i {
  transition: 0.2s;
}

.header-bar-icon i:hover {
  cursor: pointer;
  color: #f56e47;
}

.dive-in {
  transform: rotate(180deg);
  color: #f56e47;
}

.header-bar-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hidden {
  visibility: hidden;
}

.header-bar-menu>div {
  background: rgba(0, 90, 130, 0.9);
  width: 100vw;
  height: 100vh;
  display: flex;
  flex: none;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.header-bar-menu>div>ul>li {
  list-style: none;
  color: white;
  font-size: 1.5rem;
  padding: 1rem;
}

.header-bar-menu>div>ul>li>a {
  color: inherit;
  text-decoration: none;
  transition: color 0.3s ease;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
  font-size: 1.5rem;
}

/* project page */

.project .project-canvas {
  height: 100vh;
  width: 100%;
  position: fixed;
  z-index: -1;
}

.project .intro-page {
  width: 100%;
  margin: 0;
}

.project .project-main {
  width: 80%;
  height: 100vh;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.project .project-main h1 {
  font-family: "Bebas Neue", cursive;
  color: #e77557;
  font-size: 6rem;
  text-align: center;
}

.project .project-main p {
  font-family: "Bebas Neue", cursive;
  color: #e8cebc;
  font-size: 2rem;
}

.project .content-page {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.card-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.card-list .card {
  position: relative;
  width: 100%;
  height: 400px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.8);
}

.card-list .card .outlook {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card-list .card:hover .outlook.outlook2 button {
  display: block;
  border: none;
  padding: 5px 10px;
  background: rgba(0, 0, 0, 0);
  transition: all 0.3s ease;
  font-size: 1.2rem;
}

.card-list .card .outlook.outlook2 button a {
  color: white;
}

.card-list .card .outlook.outlook2 button:hover {
  border: 2px solid white;
  border-radius: 5px;
}

.card-list .card .outlook.outlook1 {
  padding: 20px;
}

.card-list .card .outlook.outlook1 .content {
  margin: 0 10px;
  padding: 0 15px;
}

.card-list .card .outlook.outlook1 .content li {
  font-size: 1.3rem;
  line-height: 1.3;
}

.card-list .card .outlook.outlook2 {
  background: #a5bd9b;
  background-size: 100%;
  transition: 0.4s;
  margin: 0;
  padding: 0;
}

.card-list .card:hover .outlook.outlook2 {
  height: 60px;
}

.card-list .card .outlook.outlook2 h2,
.card-list .card .outlook.outlook2 h3 {
  margin: 0;
  padding: 0;
  font-size: 3em;
  color: white;
  transition: font-size 0.4s ease;
  text-align: center;
  font-family: "Bebas Neue", cursive;
  overflow: hidden;
}

.card-list .card .outlook.outlook2 h2 {
  font-size: 4em;
  text-decoration: underline overline;
}

.card-list .card:hover .outlook.outlook2 h2 {
  font-size: 2em;
  text-decoration: none;
}

.card-list .card:hover .outlook.outlook2 h3,
.card-list .card:hover .outlook.outlook2 h2,
.card-list .card .outlook.outlook2 button {
  display: none;
}

/* foot */
.footage {
  height: 80px;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footage p {
  margin-top: 10px;
}

.footage i {
  color: white;
}

/* Customize Screen Size */

@media screen and (max-width: 1000px) {
  .nav-bar {
    background-color: rgba(0, 0, 0, 0);
  }

  .nav-bar-list {
    height: 100%;
    width: 200px;
    position: fixed;
    left: -140px;
    top: 60px;
    flex-direction: column;
    justify-content: start;
    transition: left 0.5s ease-in-out 0s;
  }

  .active {
    left: 0px;
  }

  .nav-bar-list li {
    width: 100%;
    padding-top: 15px;
    margin-top: 10px;
  }

  .nav-bar-list li a {
    margin: auto;
    display: flex;
    padding: 0 5px;
    flex-direction: row-reverse;
    justify-content: space-between;
  }

  .expand-icon-cell {
    display: block;
    position: absolute;
    top: 40%;
    left: 18px;
  }

  .about-me-page {
    grid-template-columns: 70px auto;
  }

  .skills-container {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 500px) {
  .home-brief-card .next-page-icon {
    display: none;
  }

  .home-brief-card {
    padding: 0 20px;
  }

  .home-brief-card h1 {
    font-size: 2.5rem;
  }

  .home-brief-card p {
    font-size: 1.5rem;
  }

  .about-me-page {
    grid-template-columns: 70px auto;
    gap: 0;
  }

  .summary .name {
    font-size: 4rem;
  }

  .summary .email {
    padding-top: 20px;
    font-size: 1.5rem;
  }

  .summary .bio {
    font-size: 1rem;
  }

  .about-me-content>p.topic {
    font-size: 3rem;
  }

  /* about section flash-card */
  .about-card-container {
    grid-template-columns: 1fr;
    margin-bottom: 5rem;
  }

  .about-card-container>.period {
    grid-column-start: 1;
    grid-row-start: 3;
    grid-row-end: 4;
    font-size: 1rem;
    margin-bottom: 2rem;
  }

  .about-card-container>.topic {
    font-size: 1.5rem;
  }

  .about-card-container>.option {
    font-size: 1.2rem;
    margin: 0;
  }

  .about-card-container>.content {
    font-size: 1rem;
  }

  .about-card-container>.content>.detail-list li {
    font-size: 1rem;
    padding: 0.05rem;
  }

  .project .project-main h1 {
    font-size: 4rem;
  }

  .project .project-main p {
    font-size: 1.5rem;
  }
}